import React from "react";
export class Toggle extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isToggle: true };
  }
  handleClick = () => {
    this.setState((prevState) => ({
      isToggle: !prevState.isToggle,
    }));
  };
  render() {
    return (
      <div onClick={this.handleClick}>{this.state.isToggle ? "No" : "yse"}</div>
    );
  }
}
