import React, { Suspense, useEffect, useState, lazy } from "react";
import { InterviewOne } from "./components/InterviewOne";
import { Toggle } from "./components/InterviewClass";
import { InterviewFanyBorder } from "./components/InterviewFanyBorder";
// import { SplitPane } from "./components/SplitPane";
import { Button } from "antd";

const SplitPane = React.lazy(() => import("./components/SplitPane"));

export const Interview = () => {
  const [timeList, setTimeList] = useState(new Date().toLocaleTimeString());
  const [propsList, setPropsList] = useState("propsList");
  useEffect(() => {
    let timeoutProps = setInterval(() => {
      setTimeList(new Date().toLocaleTimeString());
    }, 1000);
    return () => {
      clearTimeout(timeoutProps);
    };
  }, [timeList]);

  return (
    <div>
      <Button>Interview</Button>
      <InterviewOne name="interview children component" />
      <InterviewOne name="interview children component" propsList={propsList} />
      <InterviewOne name="interview children component" />
      <InterviewOne name="interview children component" />
      <div>{timeList}</div>
      <Toggle />
      <InterviewFanyBorder>
        <span>this is children1</span>
        <span>this is children2</span>
      </InterviewFanyBorder>
      <Suspense fallback={<div>loading...</div>}>
        <SplitPane
          left={<Toggle />}
          right={<InterviewFanyBorder>children right</InterviewFanyBorder>}
        />
      </Suspense>
    </div>
  );
};
