import highlight from "highlight.js";
import React, { useEffect, useState } from "react";
import { add } from "tsUtils/tsUtils";
export function One() {
  useEffect(() => {
    console.log(add(1, 3));
    console.log(1);
  }, []);
  useEffect(() => {
    // 配置 highlight.js
    highlight.configure({
      // 忽略未经转义的 HTML 字符
      ignoreUnescapedHTML: true,
    });
    // 获取到内容中所有的code标签
    const codes = document.querySelectorAll(".dg-html pre code");
    codes.forEach((el) => {
      // 让code进行高亮
      highlight.highlightElement(el as HTMLElement);
    });
  }, []);
  const content = `
    <pre> 
      <code>function getItem(
        label: React.ReactNode,
        key: React.Key,
        icon?: React.ReactNode,
        children?: MenuItem[],
        type?: "group"
      ): MenuItem {
        return {
          key,
          icon,
          children,
          label,
          type,
        } as MenuItem;
      }</code>
    </pre>  
    `;

  return (
    <div className="dg-html">
      <h4>Question</h4>
      <div dangerouslySetInnerHTML={{ __html: content }} />
    </div>
  );
}
